import { UseCase } from 'types';
import { Result, err, ok } from 'neverthrow';
import { ABLParserError, ILParserError, RawInput } from './types';
import { ILBLock } from './il';
import { ABLParser } from './ablParser';

export type FrontedError = ABLParserError | ILParserError;

export class Frontend implements UseCase<RawInput[], ILBLock[], FrontedError> {
  constructor(private readonly ablParser: ABLParser) {}

  async execute(data: RawInput[]): Promise<Result<ILBLock[], FrontedError>> {
    try {
      const ablParserResult = await this.ablParser.execute(data);
      if (ablParserResult.isErr()) {
        return err(new ABLParserError(ablParserResult.error.join('\n')));
      }

      const abls = ablParserResult.value;
      const ilBlocks = abls.map((abl) => abl.toIL());

      return ok(ilBlocks);
    } catch (e) {
      return err(new ILParserError(e.message));
    }
  }
}
