import { Frontend, ILBLock } from 'frontend';
import { err, ok, Result } from 'neverthrow';
import { BlockFile, CodeFile } from 'reactBackend';
import { UseCase } from 'types';
import { Backend } from 'types/backend';
import { YamlToObject } from 'types/yamlToObject';

export interface CompilerOutput {
  input: Record<string, unknown>[];
  intermediateLanguage: ILBLock[];
  transformedBlockFiles: BlockFile[];
  codeFiles: CodeFile[];
}

export class Compiler implements UseCase<string, CompilerOutput> {
  constructor(
    private yamlParser: YamlToObject,
    private frontend: Frontend,
    private backend: Backend,
  ) {
    this.execute = this.execute.bind(this);
  }

  async execute(input: string): Promise<Result<CompilerOutput, string>> {
    const rawObjects = await this.yamlParser.execute(input);

    if (rawObjects.isErr()) {
      return err(`cannot parse input yaml: ${rawObjects.error}`);
    }

    const ilBlocks = await this.frontend.execute(rawObjects.value);

    if (ilBlocks.isErr()) {
      return err(`cannot parse to intermediate language : ${ilBlocks.error}`);
    }

    const backendResult = await this.backend.execute(ilBlocks.value);

    if (backendResult.isErr()) {
      return err(
        `cannot parse into target language code : ${backendResult.error}`,
      );
    }

    const { transformedBlockFiles, codeFiles } = backendResult.value;

    const result: CompilerOutput = {
      input: rawObjects.value,
      intermediateLanguage: ilBlocks.value,
      transformedBlockFiles,
      codeFiles,
    };

    return ok(result);
  }
}
