import { Compiler } from 'compiler';
import { Frontend, ABLParser } from 'frontend';
import {
  ReactCodeGenerator,
  ReactTransformer,
  ReactBackend,
} from 'reactBackend';
import 'reflect-metadata';
import { YamlParser } from 'yamlParser';

const yamlParser = new YamlParser();

const ablParser = new ABLParser();
const frontend = new Frontend(ablParser);

const reactCodeGenerator = new ReactCodeGenerator();
const reactTransformer = new ReactTransformer();
const backend = new ReactBackend(reactTransformer, reactCodeGenerator);
const compiler = new Compiler(yamlParser, frontend, backend);

export const compile = compiler.execute;
