/* eslint-disable @typescript-eslint/no-use-before-define */
import {
  ContainerElement, UIBlock, Element, BlockElement,
} from 'frontend';
import { parseBlockFileName, parseContainerLayout } from './util';
import { Import } from '../tl';

export function defaultReactImport(): Import {
  return {
    default: 'react',
    from: 'react',
  };
}

export function blockImport(element: BlockElement): Import {
  return {
    from: parseBlockFileName(element.name),
    default: element.name,
  };
}

export function containerImport(container: ContainerElement): Import {
  const componentName: string = parseContainerLayout(container.layout);
  const layoutImport: Import = {
    from: 'uilib',
    named: new Set<string>([componentName]),
  };
  return layoutImport;
}

export function parseImports(view: Element): Import[] {
  switch (view.type) {
    case 'block_element':
      return [blockImport(view)];
    case 'container_element': {
      const layoutImport = containerImport(view);
      const childrenImport = view.children
        .map((element) => parseImports(element))
        .flat();
      return [layoutImport, ...childrenImport];
    }
    case 'label_element':
    default:
      return [];
  }
}

export function parseUIBlockImports(uiBlock: UIBlock): Import[] {
  const reactImport = defaultReactImport();
  const componentImport = parseImports(uiBlock.body.view);
  return [reactImport, ...componentImport];
}
