import { ContainerElementLayout } from 'frontend';
import { assertNever } from 'types';
import { ElementName } from '../tl';

export function parseContainerLayout(
  layout: ContainerElementLayout,
): ElementName {
  switch (layout) {
    case 'flex_column':
      return 'FlexColumn';
    case 'flex_row':
      return 'FlexRow';
    default:
      return assertNever();
  }
}

export function parseBlockFileName(
  filename: string,
  extension: string = '',
): string {
  return `components/${filename.toLowerCase()}${extension}`;
}
