import { Result, err, ok } from 'neverthrow';
import { ILBLock } from 'frontend';
import { Backend, BackendOutput } from 'types/backend';
import { ReactTransformer } from './transformer';
import { ReactCodeGenerator } from './codeGenerator';

export class ReactBackend implements Backend {
  constructor(
    private readonly reactTransformer: ReactTransformer,
    private readonly reactCodeGenerator: ReactCodeGenerator,
  ) {}

  execute(input: ILBLock[]): Result<BackendOutput, string> {
    const tl = this.reactTransformer.execute(input);

    if (tl.isErr()) {
      return err(tl.error);
    }

    const codefiles = this.reactCodeGenerator.execute(tl.value);

    if (codefiles.isErr()) {
      return err(codefiles.error);
    }

    const result: BackendOutput = {
      codeFiles: codefiles.value,
      transformedBlockFiles: tl.value,
    };

    return ok(result);
  }
}
