import { ok, Result } from 'neverthrow';
import { BlockFile, Import, Element } from 'reactBackend';
import prettier from 'prettier';
import parserBabel from 'prettier/parser-babel';
import { UseCase } from 'types';
import { CodeFile } from '../codeFile';

export function importToSrt(item: Import): string {
  const hasNamed = !!item.named?.size;
  const named = Array.from(item.named || []).join(',');
  const openBrace = hasNamed ? '{' : '';
  const closeBrace = hasNamed ? '}' : '';
  const namedPart = `${openBrace} ${named} ${closeBrace}`;
  const defaultPart = `${item.default || ''}`;
  const joinPart = namedPart.trim() && defaultPart.trim() ? ',' : '';

  let importPart = `${namedPart.trim()}`;
  if (joinPart) {
    importPart += ` ${joinPart} ${defaultPart}`;
  } else {
    importPart += `${defaultPart}`;
  }

  return `import ${importPart} from '${item.from}';`;
}

export function parseImport(blockFile: BlockFile): string {
  const imports = blockFile.imports.map((item) => importToSrt(item));
  return imports.join('\n');
}

export function parseAttributes(attributes?: Map<string, string>): string {
  if (!attributes) {
    return '';
  }
  const attributesStr = Array.from(attributes)
    .map((attr) => `${attr[0]}="${attr[1]}"`)
    .join(' ');

  if (attributesStr.trim().length === 0) {
    return '';
  }

  return ` ${attributesStr} `;
}

export function parseElement(element: Element): string {
  if (!element) {
    return '';
  }

  if (typeof element === 'string') {
    return element;
  }

  const innerElements = (element.innerValue || [])
    .map((e) => parseElement(e))
    .join('\n');
  const isSelfClosingTag = innerElements.length === 0;

  const attributes = parseAttributes(element.attributes);

  const openTag = `<${element.name}${attributes}${
    isSelfClosingTag ? ' /' : ''
  }>`;

  const closingTag = isSelfClosingTag ? '' : `</${element.name}>`;

  const elements = [openTag, innerElements, closingTag].filter(
    (i) => i.length > 0,
  );

  return elements.join('\n');
}

export function parseReactFunc(blockFile: BlockFile): string {
  const funcDefinition = `function ${blockFile.name}(){`;

  const funcClosing = '}';

  const view = parseElement(blockFile.view).trim();
  const viewPart = view ? `return (\n${view}\n)` : 'return (\n<></>\n)';

  return `${funcDefinition}\n${viewPart}\n${funcClosing}`;
}

export function formatCode(code: string): string {
  return prettier.format(code, {
    singleQuote: true,
    trailingComma: 'all',
    tabWidth: 2,
    semi: true,
    printWidth: 80,
    parser: 'babel',
    plugins: [parserBabel],
  });
}

export function parseCodeFile(blockFile: BlockFile): CodeFile {
  const importPart = parseImport(blockFile);
  const reactFuncPart = parseReactFunc(blockFile);
  const exportPart = `export default ${blockFile.name};`;

  const rawBody = [importPart, '', reactFuncPart, '', exportPart].join('\n');
  const body = formatCode(rawBody);
  const { path, filename } = blockFile;

  return {
    path,
    filename,
    body,
  };
}

export class ReactCodeGenerator
implements UseCase<BlockFile[], CodeFile[], string> {
  execute(input: BlockFile[]): Result<CodeFile[], string> {
    const codeFiles = input.map(parseCodeFile);
    return ok(codeFiles);
  }
}
