import { transformAndValidate } from 'class-transformer-validator';
import { Result, ResultAsync, combineWithAllErrors } from 'neverthrow';
import { UseCase } from 'types';
import { ABLBlock } from '../abl';
import { RawInput } from '../types';

export class ABLParser implements UseCase<RawInput[], ABLBlock[], string[]> {
  async execute(rawInputs: RawInput[]): Promise<Result<ABLBlock[], string[]>> {
    const abls = rawInputs.map((data) => this.toABL(data));
    return combineWithAllErrors(abls).mapErr((e) => e);
  }

  private toABL(inputData: RawInput): ResultAsync<ABLBlock, string> {
    const block = {
      block: inputData,
    };
    return ResultAsync.fromPromise(
      transformAndValidate(ABLBlock, block, {
        transformer: { excludeExtraneousValues: true },
      }),
      (e) => JSON.stringify(e),
    );
  }
}
