/* eslint-disable @typescript-eslint/no-use-before-define */
import { ok, Result } from 'neverthrow';
import { assertNever, UseCase } from 'types';
import {
  BlockElement,
  ContainerElement,
  Element,
  ILBLock,
  LabelElement,
  UIBlock,
} from 'frontend';
import { parseUIBlockImports } from './import';
import { BlockFile, TagElement } from '../tl';
import { parseContainerLayout } from './util';

export function parseLabelElement(labelElement: LabelElement): TagElement {
  return {
    name: 'div',
    innerValue: [labelElement.value],
  };
}

export function parseBlockElement(blockElement: BlockElement): TagElement {
  return {
    name: blockElement.name,
  };
}

export function parseContainerElement(
  containerElement: ContainerElement,
): TagElement {
  const innerValue = containerElement.children.map((element) => parseElement(element));
  return {
    name: parseContainerLayout(containerElement.layout),
    innerValue,
  };
}

export function parseElement(element: Element): TagElement {
  switch (element.type) {
    case 'block_element':
      return parseBlockElement(element);
    case 'label_element':
      return parseLabelElement(element);
    case 'container_element':
      return parseContainerElement(element);
    default:
      return assertNever();
  }
}

export function parseUIBlock(block: UIBlock): BlockFile {
  const imports = parseUIBlockImports(block);
  const view = parseElement(block.body.view);
  const { name } = block;
  return {
    name,
    filename: `${name.toLowerCase()}.jsx`,
    path: 'components',
    imports,
    view,
  };
}

export class ReactTransformer
implements UseCase<ILBLock[], BlockFile[], string> {
  execute(input: ILBLock[]): Result<BlockFile[], string> {
    const blockFiles = input.map((block) => parseUIBlock(block));
    return ok(blockFiles);
  }
}
