import 'reflect-metadata';
import { Expose, Type } from 'class-transformer';
import {
  IsArray,
  IsEnum,
  IsIn,
  IsNotEmpty,
  IsOptional,
  IsString,
  ValidateNested,
} from 'class-validator';
import {
  BlockElement,
  ContainerElement,
  ContainerElementLayout,
  Element,
  LabelElement,
  UIBlock as ILUIBlock,
  UIBody,
} from 'frontend/il';
import { IncompatibleWith } from 'incompatiblewith';


interface ILParsable<T> {
  toIL(): T;
}

export enum ComponentType {
  UI_COMPONENT = 'ui',
}

export type ViewType = 'label' | 'block' | 'container';

export function isValidBlockName(name: string): boolean {
  const pascalCaseRegex = new RegExp('^([A-Z][a-z]+)(([A-Z][a-z]+))*([A-Z])?');
  const onlyAlphabetRegex = new RegExp('^[a-zA-Z]+$');

  return onlyAlphabetRegex.test(name) && pascalCaseRegex.test(name);
}

export class BlockView implements ILParsable<BlockElement> {
  @IsString()
  @IsNotEmpty()
  @Expose()
  @IsNotEmpty()
  name: string;

  toIL(): BlockElement {
    if (!isValidBlockName(this.name)) {
      throw Error(`invalid BlockView.name "${this.name}"`);
    }

    return {
      type: 'block_element',
      name: this.name
    };
  }
}

export class ViewInnerValue implements ILParsable<string> {
  @IsString()
  @Expose()
  @IsNotEmpty()
  static: string;

  toIL(): string {
    return this.static;
  }
}

export class LabelView implements ILParsable<LabelElement> {
  @ValidateNested()
  @Expose()
  @Type(() => ViewInnerValue)
  @IsNotEmpty()
  value: ViewInnerValue;

  toIL(): LabelElement {
    return {
      type: 'label_element',
      value: this.value.toIL(),
    };
  }
}

export enum ContainerViewLayout {
  FLEX_ROW = 'flex_row',
  FLEX_COLUMN = 'flex_column',
}

export class ContainerView implements ILParsable<ContainerElement> {
  @IsEnum(ContainerViewLayout)
  @IsNotEmpty()
  @Expose()
  @IsNotEmpty()
  layout: ContainerViewLayout;

  @ValidateNested({ each: true })
  @IsArray({})
  @IsNotEmpty()
  @Expose()
  @Type(() => View)
  @IsNotEmpty()
  children: View[];

  toIL(): ContainerElement {
    return {
      type: 'container_element',
      layout: this.containerElementLayout(),
      children: this.children.map((c) => c.toIL()),
    };
  }

  containerElementLayout(): ContainerElementLayout {
    if (this.layout === ContainerViewLayout.FLEX_COLUMN) {
      return 'flex_column';
    }
    return 'flex_row';
  }
}

export class View implements ILParsable<Element> {
  @IsOptional()
  @ValidateNested()
  @IncompatibleWith(['label', 'block'])
  @Expose()
  @Type(() => ContainerView)
  container?: ContainerView;

  @IsOptional()
  @ValidateNested()
  @IncompatibleWith(['container', 'block'])
  @Expose()
  @Type(() => LabelView)
  label?: LabelView;

  @IsOptional()
  @ValidateNested()
  @IncompatibleWith(['label', 'container'])
  @Expose()
  @Type(() => BlockView)
  block?: BlockView;

  toIL(): Element {
    const view = this.block || this.container || this.label;
    if (!view) {
      throw Error('view is not supported.');
    }
    return view?.toIL();
  }
}

export class UISpec implements ILParsable<UIBody> {
  @ValidateNested()
  @Expose()
  @Type(() => View)
  @IsNotEmpty()
  view: View;

  toIL(): UIBody {
    return {
      view: this.view.toIL(),
    };
  }
}

export class Block {
  @IsString()
  @IsEnum(ComponentType)
  @Expose()
  @IsNotEmpty()
  kind: ComponentType;

  @IsString()
  @IsNotEmpty()
  @Expose()
  @IsNotEmpty()
  name: string;
}

export class UIBlock extends Block implements ILParsable<ILUIBlock> {
  @IsString()
  @IsIn(['ui'])
  @IsNotEmpty()
  @Expose()
  @IsNotEmpty()
  kind: ComponentType.UI_COMPONENT;

  @IsString()
  @IsNotEmpty()
  @Expose()
  @IsNotEmpty()
  name: string;

  @ValidateNested()
  @Expose()
  @IsNotEmpty()
  @Type(() => UISpec)
  spec: UISpec;

  toIL(): ILUIBlock {
    if (!isValidBlockName(this.name)) {
      throw Error(`invalid UIBlock.name "${this.name}"`);
    }

    const body = this.spec.toIL();
    return {
      type: 'ui_block',
      name: this.name,
      body,
    };
  }
}

export class ABLBlock implements ILParsable<ILUIBlock> {
  @ValidateNested({ each: true })
  @IsNotEmpty()
  @Expose()
  @Type(() => Block, {
    keepDiscriminatorProperty: true,
    discriminator: {
      property: 'kind',
      subTypes: [{ value: UIBlock, name: 'ui' }],
    },
  })
  block: UIBlock;

  toIL(): ILUIBlock {
    return this.block.toIL();
  }
}
