import jsyaml from 'js-yaml';
import { err, ok, Result } from 'neverthrow';
import { YamlToObject } from 'types/yamlToObject';

export class YamlParser implements YamlToObject {
  execute(yaml: string): Result<Record<string, unknown>[], string> {
    try {
      const arr = jsyaml.safeLoadAll(yaml);
      return ok(arr);
    } catch (e) {
      return err('cannot parse input yaml into object.');
    }
  }
}
